body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}
.map {
  width: 100%;
  height: 100vh;
  position: relative;
}
.location-icon {
  font-size: 2rem;
  color: red;
}
.loader {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-width: 100vh;
  height: 100vh;
}
.loader img {
  width: 400px;
}
/* .loader h1{
  margin-top: -100px;
} */
.location-info {
  position: absolute;
  top: 50px;
  right: 50px;
  width: 400px;
  min-height: 200px;
  padding: 20px;
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 10px;
  font-size: 18px;
  color: #fff;
}
.location-info ul {
  list-style-type: none;
  padding: 0;
}
.location-info li {
  padding: 5px 0;
}

.header__icons {
  display: flex;
  justify-content: center;
  background-color: red;
}
.header__icon2 {
  display: flex;
  align-items: center;
  float: right;
  margin-left: 100px;
  color: #fff;
}
.location-icon2 {
  color: #fff;
  font-size: 1.5rem;
}
span {
  padding-right: 15px;
  color: #fff;
}
h3 {
  color: #fff;
}
